.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 9999;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.8);
    animation-name: appear;
    animation-duration: 300ms;
  }
  
  .modal-dialog {
    width: 100%;
    max-width: 550px;
    background: white;
    position: relative;
    margin: 0 20px;
    max-height: calc(100vh - 40px);
    text-align: left;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: slide-in;
    animation-duration: 0.5s;
  }
  
  .modal-header,
  .modal-footer {
    display: flex;
    align-items: center;
    padding: 1rem;
  }
  .modal-header {
    border-bottom: 1px solid #dbdbdb;
    justify-content: space-between;
     h3{
        color: black;
        font-size: 1.5rem;
    }
    span {
      border: solid 2px black;
      border-radius: 50%;
      margin-right: 1px;
      font-size:1.5rem;
      padding: 2px 10px; 
      &:hover {
        font-size: 1.2rem;
        background-color: black;
        color:white;
        
      }
        }
  }
  .modal-footer {
    border-top: 1px solid #dbdbdb;
    justify-content: flex-end;
    background-color: #fff;
    width: 100%;
    background-color: #fff;
    display: flex;
    justify-content: space-around;
    align-items: center;
    button {
      padding: .2rem;
      width: 40%;
      font-size: 1rem;
      background-color: white;
      color: black;
      border: solid 2px black;
      cursor: pointer;
      &:hover {
        font-size: 1.2rem;
        background-color: black;
        color:white;
        
      }
    }
  }
    
  .modal-close {
    cursor: pointer;
    padding: 1rem;
    margin: -1rem -1rem -1rem auto;
  }
  .modal-body {
    overflow: auto;
  }

  .modal-content {
    padding: 1rem;
    textarea {
        width: 100%;
        height: 8rem;
        outline: none;
        border: none;
        border-radius: .3rem;
        font-size: 1rem;
      }
  }
  
  .modal-title {
    margin: 0;
  }
  


  @keyframes appear {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  @keyframes slide-in {
    from {
      transform: translateY(-150px);
    }
    to {
      transform: translateY(0);
    }
  }